.avatar{
  display: flex;
  padding: 20px 0 20px 30px;
  margin-bottom: 30px;
  min-height: 150px;
}
.photo{
  width: 120px;
  height: 120px;
  margin-right: 30px;
  background-color: cadetblue;
  border-radius: 5px;
}
.photo img{
  margin: 10px;
  width: 100px;
  height: 100px;
  border-radius: 5px;
}
.name{
  font-size: 26px;
  color: aliceblue;
  margin-bottom: 15px;
}
.text{
  color: gainsboro;
  margin-bottom: 5px;
  font-size: 17px;
}
.not_job,
.job{
  position: relative;
}
.not_job img {
  position: absolute;
  left: 110px;
  top: 2px;
}
.job img {
  position: absolute;
  left: 90px;
  top: 3px;
}