.new_post{
  background-color: lightblue;
  width: 600px;
  border-radius: 10px;
  height: 100px;
  margin: 50px auto 0;
  padding: 10px 20px 20px;
  position: relative;
}
.new_post img {
  position: absolute;
  left: -110px;
  top: 0;
  border-radius: 10px;
}
.like{
  position: absolute;
  bottom: 3px;
  right: 10px;
  color: brown;
  font-weight: 500;
  cursor: pointer;
}
.like span {
  color: crimson;
  cursor: default;
}
